<template>

<div class='metamap'>
  <img :src="molimage" />
   <svg data-v=""
         xmlns="http://www.w3.org/2000/svg"
         x="0px"
         y="0px"
         :height="height"
         :width="width"
         :transform="transform"
         :aria-label="map.label"
         role="presentation"
         class="border"
         v-dragged="onDragged"
    >
         <defs
     id="defs22468">
    <marker
       style="overflow:visible"
       refY="0"
       refX="0"
       orient="auto"
       id="Arrow1Lstart">
      <path
         inkscape:connector-curvature="0"
         transform="matrix(0.8,0,0,0.8,3,0)"
         style="fill-rule:evenodd;stroke:#000000;stroke-width:1.00000003pt;marker-start:none"
         id="path_arrowstart-26"
         d="M 0,0 1.5,-1.5 -3.75,0 1.5,1.5 Z" />
    </marker>
    <marker
       style="overflow:visible"
       refY="0"
       refX="0"
       orient="auto"
       id="Arrow1Lend">
      <path
         inkscape:connector-curvature="0"
         transform="matrix(-0.8,0,0,-0.8,-3,0)"
         style="fill-rule:evenodd;stroke:#000000;stroke-width:1.00000003pt;marker-start:none"
         id="path_arrowend-88"
         d="M 0,0 1.5,-1.5 -3.75,0 1.5,1.5 Z" />
    </marker>
  </defs>

        <title id="metabolic" data-v="" lang="en">Metabolic Network</title>

        <slot name="before"/>
        <path
                v-for="(reac, index) in map.path"
                :id="reac.id"
                :key="reac.id"
                :name="reac.name"
                :d="reac.d"
                :style="reac.style"
                v-on="$listeners"
                @click.prevent="pathclick(index)"
                class="reac_path"
        />
        <text
                v-for="(reac, index) in map.text"
                :id="reac.id"
                :key="reac.id"
                :x="reac.x"
                :y="reac.y"
                :style="reac.style"
                class="textcaption"
                v-text="reac.data"
                @click.prevent="textclick(index)"
        ></text>
        <circle
                v-for="reac in map.circle"
                :id="reac.id"
                :key="reac.id"
                :cx="reac.cx"
                :cy="reac.cy"
                :r="reac.r"
                :style="reac.style"
                :transform="reac.transform"
                class="metabolites"
            />

        <slot name="after"/>
    </svg>
</div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'
import VDragged from 'v-dragged'

Vue.use(VDragged)

export default {
  name: 'Map',
  data: function () {
    return {
    label: 'test',
    viewBox: "0 -200 322.8 381",
    transform: "translate(0,0) scale(2)",
    height: "581",
    width: "322",
    isDragging: false,
    offx: 0,
    offy: 0,
    cur_offx: 0,
    cur_offy: 0,
    map: {},
    molimage: "",
    scroll: 0
  };
  },
  created () {
    this.fetchData()
    //window.addEventListener('scroll', this.handleScroll);
  },
  watch: {
    // call again the method if the route changes
    '$route': 'fetchData'
  },
  mounted () {
    this.fetchData()
    document.addEventListener('scroll', this.onScroll)
  },
  methods: {
    fetchData: function () {
      axios.get('http://localhost:5000/api')
          .then(response => {
                    Vue.set(this, 'map', response.data)
          }
                )
    },
    viewboxchange: function() {
      console.log(this.viewBox.split(' '))
      var cur = this.viewBox.split(' ').map(function(x) {
          return parseInt(x, 10);
          });
      cur = cur.map(function(x) { return x * 0.8});
      console.log(cur)
      var viewBox = cur.join(' ')
      console.log(viewBox)
      this.viewBox = viewBox
    },
    leftclick: function() {
      this.transform= "translate(0,0) scale(1.2)"
    },
    pathclick: function(index) {
      var style = this.map.path[index].style;
      style = style.replace('stroke:#000000;','stroke:#0000ff;');
      style = style.replace('stroke-width: 2','stroke-width: 3');
      this.map.path[index].style = style;
      console.log(style)
    },
    // eslint-disable-next-line no-unused-vars
    textclick: function(index) {
      var mol = this.map.text[index].data;
      axios.post('http://localhost:5000/smiles', { molname: mol })
        .then(response => {
            Vue.set(this, 'molimage', response.data)
        })
    },
    // eslint-disable-next-line no-unused-vars
    onDragged({ el, deltaX, deltaY, offsetX, offsetY, clientX, clientY, first, last }) {
      if (first) {
        this.isDragging = true
        this.cur_offx = this.offx
        this.cur_offy = this.offy
        return
      }
      if (last) {
        this.isDragging = false
        return

      }
      this.offx = this.cur_offx+offsetX
      this.offy = this.cur_offy+offsetY
      this.transform = "translate("+ (this.cur_offx+offsetX) +","+ (this.cur_offy+offsetY)+") scale(2)"
    },
    onScroll: function () {
      this.scroll = window.scrollY
      console.log(this.scroll)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
