    .reac_path {
    stroke: #aa00aa ;
    }
    .reac_path:hover {
    stroke: #bf00ff !important;
    stroke-width: 0.8px !important;
    }
    .textcaption:hover {
    fill: #8a2be2 !important;
    font-size: 10px !important;
    }
    .metabolites:hover {
    fill: #7fffd4 !important;
    }
    .container {
    padding-top: 2px;
    top:0;
    }
    .border {
    border: 2px;
    }
</style>
